.list {
    list-style: none;
    padding: 0;
    margin-top: 15px;
}

img {
    display: block;
    width: 100%;
    height: 380px;
    object-fit: cover;
    border-radius: 5px;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-right: -20px;
}

.gallery__item {
    margin-right: 20px;
    margin-top: 20px;
    flex-basis: calc((100% - 20px *3) / 3);
}

.photo-card {
    height: 100%;
    object-fit: cover;
}

.stats {
    display: flex;
    flex-direction: row;
    justify-content: center;
    border: 1px solid white;
    background-color: #3a75c4;
}

.stats-item:not(:last-child) {
    margin-right: 40px;
}

.stats-item {
    display: flex;
    align-items: center;
    color: wheat;
}

.material-icons {
    margin-right: 10px;
    color: white;
}